.doc-block h5,
.doc-block h6 {
  margin-top: 24px;
  margin-bottom: 16px;
}
.doc-block pre{
  padding: 10px;
  overflow: auto;
  font-size: 85%;
  line-height: 1.45;
  background-color: #f6f8fa;
  border-radius: 3px;
}
.m-input-block,.m-input-block m-input {
    display: block
}
.m-input {
  display: inline-block;
  padding: 5px 12px;
  font-size: 14px;
  font-family: inherit;
  line-height: 1.42857143;
  height: 36px;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  vertical-align: middle;
  outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 0 0 0 rgba(44,147,250,.18);
          box-shadow: 0 0 0 0 rgba(44,147,250,.18);
  -webkit-transition: border,-webkit-box-shadow .5s ease;
  transition: border,-webkit-box-shadow .5s ease;
  transition: border,box-shadow .5s ease;
  transition: border,box-shadow .5s ease,-webkit-box-shadow .5s ease
}
.m-input.m-input-textarea {
  padding: 5px 12px;
  min-height: 54px;
  height: 54px
}
.m-input:focus,.m-input:hover {
  border-color: #29a0f2
}
.m-input:focus {
  -webkit-box-shadow: 0 0 0 3px rgba(44,147,250,.18);
          box-shadow: 0 0 0 3px rgba(44,147,250,.18)
}
.m-input.disabled,.m-input:disabled {
  background: #f9f9f9;
  border-color: #ccc;
  cursor: not-allowed
}